import logo from './logo.svg';
import './App.css';
import React from 'react'
import axios from 'axios'

const products = [
  {
    id:1,
    name:'Veltlínske zelené',
    info:'suché, biele, 12.59% alk',
    price:8.5,
    priceWithTax:10.2
  },
  {
    id:2,
    name:'Pinot blanc',
    info:'suché, biele, 13% alk',
    price:3,
    priceWithTax:5
  },
  {
    id:3,
    name:'Frankovka modrá',
    info:'suché, červené, 12.5% alk',
    price:7,
    priceWithTax:8.4
  }
]

class App extends React.Component{
  render(){
    return <OrderForm></OrderForm>
  }
}

class OrderForm extends React.Component {
  constructor(props){
    super(props);
    this.state = {count:1,order:[]}
    this.addToOrder = this.addToOrder.bind(this)
    this.removeFromOrder = this.removeFromOrder.bind(this)
    this.setCountOfProduct = this.setCountOfProduct.bind(this)
  }

  addToOrder(id){
    const newOrder = this.state.order
    const product = products.find(prod => prod.id===id)
    if (newOrder.find(prod => prod.id===id)){
      newOrder.map(prod=>{
        if (prod.id === id){
          prod.count+=1
        }
        return prod
      })
    }
    else{
      newOrder.push({
        ...product,
        count:1
      })
    }
    this.setState({order:newOrder})
  }

  removeFromOrder(id){
    let newOrder = this.state.order
    newOrder = newOrder.filter(prod=>prod.id!==id)
    this.setState({order:newOrder})
  }

  setCountOfProduct(id,count){
    if (count === 0){
      return this.removeFromOrder(id)
    }
    let newOrder = this.state.order
    newOrder = newOrder.map(prod=>{
     if ( prod.id === id ){
       prod.count = count
     }
     return prod
    })
    this.setState({order:newOrder})
  }
  async componentDidMount(){
    const st = await axios.get('https://marhulky.sk:8443/api/store/products')
    this.setState({count:st.data.products.length})
  }
  render() {
    const productsToRender = products.map(product => {return <Product id={product.id} addToOrder={this.addToOrder} data={product}></Product>})
    const productSummariesToRender = this.state.order.map(product => {return <SummaryProduct
      id={product.id} 
      setCountOfProduct={this.setCountOfProduct}
      removeFromOrder={this.removeFromOrder} 
      data={product}></SummaryProduct>})
    return <div className='order-form'>
      <h2 className='order-heading'>Objednávka</h2>
      <div className='order-header'>
        <h3 className='product'>Produkt</h3>
        <h3 className='price'>Cena</h3>
        <div className='ks'></div>
      </div>
      {productsToRender}
      <h2 className='summary-heading'>Košík</h2>
      <div className='order-header'>
        <h3 className='product'>Produkt</h3>
        <h3 className='price'>Cena</h3>
        <h3 className='ks'>Ks.</h3>
      </div>
      {productSummariesToRender}
      <PriceSummary order={this.state.order}></PriceSummary>
    </div>
  }
}

class PriceSummary extends React.Component{
  render(){
    const order = this.props.order
    const priceWithoutTax = order.reduce((ac,prod) => {
      return ac+prod.price*prod.count
    },0).toFixed(2)+'€'
    const priceWithTax = order.reduce((ac,prod) => {
      return ac+prod.priceWithTax*prod.count
    },0).toFixed(2)+'€'
    return(
    <div className='price-summary'>
    <div className="price-block">
      <div>Cena bez DPH</div>
      <div className='price'>{priceWithoutTax}</div>
    </div>
    <div className="price-block">
      <div>Cena s DPH</div>
      <div className='price'>{priceWithTax}</div>
    </div>
    </div>
    )
  }
}

class Product extends React.Component{
  render(){
    return (
      <div className='product-cont'>
        <img className='product-img' alt='vino' src='https://tvojevino.sk/upload/products/cabernet-sauvignon-vino-bez-histaminu.png'></img>
        <div className='name-and-info'>
        <h4>{this.props.data.name}</h4>
        <div>{this.props.data.info}</div>
        </div>
        <div className='price'>{this.props.data.priceWithTax.toFixed(2)+'€'}</div>
        <div className='add-to-cart' onClick={()=>this.props.addToOrder(this.props.data.id)}><ion-icon class='add-icon' name="add-circle-outline"></ion-icon></div>
      </div>
    )
  }
}

class SummaryProduct extends React.Component{
  render(){
    return(
      <div className='product-cont'>
        <h4>{this.props.data.name}</h4>
        <div className='price'>{(this.props.data.priceWithTax * this.props.data.count).toFixed(2)+'€'}</div>
        <div className='set-quant'>{this.props.data.count}</div>
          <div class='arrows'>
            <div className='arrow-up' onClick={()=>this.props.setCountOfProduct(this.props.data.id,this.props.data.count+1)}><ion-icon name="arrow-up-outline"></ion-icon></div>
            <div className='arrow-down' onClick={()=>this.props.setCountOfProduct(this.props.data.id,this.props.data.count-1)}><ion-icon name="arrow-down-outline"></ion-icon></div>
          </div>
      </div>
    )
  }
}

export default App;
